import * as React from "react"
import "../styles/bulma.scss"
import "../styles/index.scss"
import Layout from "../components/Layout"


//  style={{backgroundImage: "linear-gradient(to bottom, rgba(200,240,255,0.5), rgba(255,255,255,0.5))"}}

const Introduction = () => <section className="section">
  <article className="container content is-max-desktop">
    <p style={{"fontSize": "1.3em"}}>
      Our works: ...
    </p>
  </article>
</section>

interface FilmProps {
  url: string,
  title_zh: string,
  title_en: string
}

const FilmData: FilmProps[] = [
  {
    title_zh: "平凡之路",
    title_en: "The Life",
    url: "https://player.bilibili.com/player.html?aid=853936742&bvid=BV1PL4y1F7Uo&cid=718158491&page=1&high_quality=1"
  }
]

const Film = ({url, title_en, title_zh}: FilmProps) => <article className="container content is-max-desktop" key={url}>
  <h2>{title_en}</h2>
  <iframe src={url} scrolling="no" border="0" frameborder="no" framespacing="0" allowfullscreen="true" width="100%" height="600">
  </iframe>
</article>

const Display = () => <section className="section">
  {FilmData.map(Film)}
</section>

// markup
const Gallery = () => {
  return (
    <Layout slug="gallery">
      <main>
        <Introduction />
        <Display />
      </main>
    </Layout>
  )
}

export default Gallery
